body {
  background-color: rgb(250, 250,250);
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.image {
  width: 270px;
  margin-top: 30px;
  margin-bottom: 30px;
}

footer{
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}

footer i, footer a{
  font-family: Arial;
  color: rgba(0, 0, 0, 0.5);
  font-size: 15px;
}

@media only screen and (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .image {
    width: 200px;
  }
}
